
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  margin: 0;
  margin-top: 60px;
  width: 100%;
}
h1 {
  text-align: center;
}
.close-btn {
  float: right;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
  font-weight: bold;
}
#tab-bar {
  position: relative;
  list-style: none;
  margin: 0;
  padding: 0 40px;
  line-height: 24px;
}
#tab-bar:before {
  z-index: 1;
}
#tab-bar:after {
  position: absolute;
  content: "";
  width: 100%;
  bottom: 0;
  left: 0;
  border-bottom: 1px solid #aaa;
  z-index: 1;
}
.tab {
  position: relative;
  z-index: 0;
  margin: 0 -3px;
  padding: 0 20px;
  border: 1px solid #aaa;
  border-top-left-radius: 6px;
  border-top-right-radius: 6px;
  background: -webkit-gradient(
    linear,
    left top, left bottom,
    color-stop(50%, rgb(235, 235, 235)),
    to(rgb(204, 203, 203))
  );
  background: linear-gradient(
    to bottom,
    rgb(235, 235, 235) 50%,
    rgb(204, 203, 203) 100%
  );
  text-shadow: 0 1px white;
  display: inline-block;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.tab:before,
.tab:after {
  position: absolute;
  bottom: -1px;
  width: 6px;
  height: 6px;
  content: " ";
  border: 1px solid #aaa;
}
.tab:before {
  left: -7px;
  border-bottom-right-radius: 6px;
  border-width: 0 1px 1px 0;
  -webkit-box-shadow: 1px 0.5px 0 rgb(204, 203, 203);
          box-shadow: 1px 0.5px 0 rgb(204, 203, 203);
}
.tab:after {
  right: -7px;
  border-bottom-left-radius: 6px;
  border-width: 0 0 1px 1px;
  -webkit-box-shadow: -1px 0.5px 0 rgb(204, 203, 203);
          box-shadow: -1px 0.5px 0 rgb(204, 203, 203);
}
.activeTab {
  z-index: 2;
  border-bottom-color: white;
  background: white;
}
.activeTab:before {
  -webkit-box-shadow: 2px 2px 0 white;
          box-shadow: 2px 2px 0 white;
}
.activeTab:after {
  -webkit-box-shadow: -2px 2px 0 white;
          box-shadow: -2px 2px 0 white;
}

